"use client";

import { User } from "better-auth";
import { Button } from "../ui/button";
import { toast } from "../layout/toast";

export default ({ user }: { user: User }) => {
  return (
    <Button
      onClick={async () => {
        await navigator.clipboard.writeText(user.id);
        toast("User id was copied to clipboard");
      }}
    >
      Copy ID
    </Button>
  );
};
